.card {
  display: flex;
  flex-direction: column;
  align-items: center;

  &--panoramic {
    margin-top: 16px;

    @media (min-width: 660px) {
      margin-top: 45px;
    }

    @media (min-width: 1200px) {
      margin-top: 55px;
    }
  }
}

.card__link {
  position: relative;
  display: block;

  @media (min-width: 660px) {
    width: 300px;
    height: 300px;
  }

  @media (min-width: 1200px) {
    width: 300px;
    height: 400px;
  }

  &--panoramic {
    width: 100%;
    height: auto;
  }
}

.card__tag {
  position: absolute;
  top: 0;
  right: 20px;
  display: block;
  width: 140px;
  height: 36px;
  padding: 7px 0;
  font-weight: 700;
  font-size: 12px;
  line-height: 24px;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #d22856;

  @media (min-width: 660px) {
    top: 55px;
  }
}

.card__image {
  display: block;
  min-width: 100%;
  height: 100%;
}

.card__info {
  display: flex;
  flex-direction: column;
  padding: 18px 0 20px 17px;
  background-color: #f2f2f2;
  width: 100%;
  max-width: 280px;
  margin: 0 auto;

  @media (min-width: 660px) {
    display: grid;
    row-gap: 1px;
    grid-template-areas: "name name" "text text" "likes likes";
    grid-template-rows: 23px 55px 20px;
    max-width: 300px;
    height: 155px;
    padding: 23px 0 23px 30px;
  }

  @media (min-width: 1200px) {
    grid-template-rows: 24px 56px 20px;
    padding-left: 26px;
  }

  &--panoramic {
    max-width: 280px;
    height: auto;
    grid-template-rows: repeat(3, auto);

    @media (min-width: 660px) {
      padding-left: 31px;
      grid-template-areas: "name likes" "text text";
      max-width: 620px;
      height: 121px;
    }

    @media (min-width: 1200px) {
      max-width: 940px;
      padding-left: 28px;
      padding-bottom: 0;
      grid-template-rows: 27px 60px;
      height: 96px;
    }
  }
}

.card__author {
  font-weight: 700;
  font-size: 14px;
  line-height: 24px;
  grid-area: name;
}

.card__time {
  font-weight: 300;
  font-size: 12px;
  line-height: 24px;
  text-transform: uppercase;
  padding-left: 0;

  &--panoramic {
    padding-left: 3px;
  }
}

.card__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  margin: 0;
  grid-area: text;
  align-self: flex-start;

  @media (min-width: 660px) {
    max-width: 250px;
  }

  &--panoramic {
    @media (min-width: 660px) {
      max-width: unset;
      margin-top: -2px;
    }
  }
}

.card__likes {
  display: flex;
  align-items: center;
  font-weight: 400;
  font-size: 12px;
  line-height: 24px;
  text-transform: uppercase;
  color: #d22856;
  margin-top: 12px;
  grid-area: likes;

  @media (min-width: 660px) {
    justify-self: flex-end;
    margin-top: 0;
    padding-right: 30px;
    padding-top: 12px;
    width: 100%;
    justify-content: space-between;
    flex-direction: row-reverse;
  }

  &--panoramic {
    @media (min-width: 660px) {
      width: 148px;
      flex-direction: row-reverse;
      padding-top: 2px;
    }

    @media (min-width: 1200px) {
      width: 150px;
      padding-bottom: 12px;
      padding-top: 14px;
      padding-right: 31px;
    }
  }

