*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

.container {
  overflow: hidden;
}
.container--inside {
  overflow: visible;
  width: 100%;
  max-width: 320px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (min-width: 660px) {
  .container--inside {
    max-width: 660px;
    padding: 0 20px;
  }
}
@media (min-width: 1200px) {
  .container--inside {
    max-width: 1200px;
    padding: 0 130px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "Opensans";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Opensans";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
@font-face {
  font-family: "Opensans";
  font-weight: 300;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
}
body {
  position: relative;
  font-family: "Opensans", sans-serif;
  font-weight: 400;
  font-style: normal;
  color: #283645;
}

.section-title {
  font-weight: 300;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  margin: 0;
}
.section-title--ad-headline {
  margin-top: 29px;
}
@media (min-width: 660px) {
  .section-title--ad-headline {
    font-size: 40px;
    line-height: 54px;
    margin-top: 37px;
  }
}
@media (min-width: 1200px) {
  .section-title--ad-headline {
    font-size: 50px;
    line-height: 68px;
    margin-top: 52px;
  }
}
.section-title--ad-subline {
  margin-top: 29px;
}
@media (min-width: 660px) {
  .section-title--ad-subline {
    font-size: 40px;
    line-height: 54px;
    max-width: 360px;
    margin: 0 auto;
  }
}
@media (min-width: 1200px) {
  .section-title--ad-subline {
    font-size: 50px;
    line-height: 68px;
    max-width: 420px;
    margin: 0 auto;
  }
}
@media (min-width: 660px) {
  .section-title--contacts {
    font-size: 40px;
    line-height: 54px;
  }
}
@media (min-width: 1200px) {
  .section-title--contacts {
    font-size: 50px;
    line-height: 68px;
    padding-top: 40px;
  }
}

.header {
  position: absolute;
  top: 0;
  z-index: 5;
  width: 100%;
  height: 66px;
  padding: 23px 0 23px 0;
  color: #ffffff;
  background-color: rgba(0, 0, 0, 0.3);
}
.header--active {
  background-color: #283645;
  height: 69px;
}
@media (min-width: 660px) {
  .header {
    height: 110px;
  }
}

.header__logo-box {
  position: relative;
  display: flex;
  align-items: flex-end;
}

.header__logo {
  display: inline-block;
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

