@media (min-width: 1200px) {
  .header__burger {
    display: none;
  }
}

.header__navigation {
  position: absolute;
  top: 100%;
  left: 0;
  display: none;
  width: 100%;
}
.header__navigation--active {
  display: block;
}
.header__navigation--active .header__navigation-item {
  background-color: #283645;
}
@media (min-width: 1200px) {
  .header__navigation {
    display: block;
    position: unset;
    margin-left: 173px;
  }
}

.header__navigation-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 1200px) {
  .header__navigation-list {
    display: flex;
    justify-content: space-between;
    height: 45px;
    margin-bottom: 3px;
  }
}

.header__navigation-item {
  padding: 15px 0;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 18px;
  line-height: 32px;
  border-top: 1px solid #000000;
  box-shadow: 0 1px 0 #000000, inset 0 1px 0 rgba(255, 255, 255, 0.15);
}
.header__navigation-item:last-child {
  border-bottom: 1px solid #000000;
}
@media (min-width: 1200px) {
  .header__navigation-item--active {
    border-bottom: 2px solid #ffffff;
  }
}
@media (min-width: 1200px) {
  .header__navigation-item {
    font-weight: 400;
    line-height: 18px;
    box-shadow: none;
    text-transform: none;
  }
}

.index-intro {
  position: relative;
}
@media (min-width: 660px) {
  .index-intro {
    background: url("../img/intro/phone.png") 50% 100% no-repeat, url("../img/intro/screen.png") right 50% bottom 56px no-repeat, #1d384b url("../img/intro/intro-background.png") center/cover no-repeat;
    padding-bottom: 82px;
    padding-left: 20px;
  }
}
@media (min-width: 1200px) {
  .index-intro {
    padding-left: 0;
    padding-bottom: 94px;
  }
}

.index-intro__presentation {
  color: #ffffff;
  padding: 90px 0 315px;
  background: url("../img/intro/phone.png") 50% 100% no-repeat, url("../img/intro/screen.png") right 50% bottom 56px no-repeat, #1d384b url("../img/intro/intro-background.png") center/cover no-repeat;
  text-align: center;
}
@media (min-width: 660px) {
  .index-intro__presentation {
    background: transparent;
    padding-bottom: 0;
    width: 660px;
    margin: 0 auto;
  }
}
@media (min-width: 1200px) {
  .index-intro__presentation {
    background: transparent;
    padding-bottom: 0;
    width: 940px;
    margin: 0 auto;
  }
}

.index-intro__slogan {
  display: block;
  font-weight: 300;
  font-size: 24px;
  line-height: 28px;
  max-width: 320px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (min-width: 660px) {
  .index-intro__slogan {
    font-size: 40px;
    line-height: 48px;
    margin-left: 0;
    max-width: 300px;
    padding: 115px 40px 0;
  }
}
@media (min-width: 1200px) {
  .index-intro__slogan {
    padding-left: 0;
    padding-right: 0;
    font-size: 50px;
    line-height: 68px;
    margin-left: 0;
    padding-top: 120px;
  }
}

@media (min-width: 660px) {
  .index-intro__download {
    width: 660px;
    margin: 0 auto;
  }
}
@media (min-width: 1200px) {
  .index-intro__download {
    width: 940px;
  }
}

.index-intro__download-inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 29px 0 31px;
  color: #ffffff;
  background: #1d2631;
  height: 272px;
}
@media (min-width: 660px) {
  .index-intro__download-inner {
    width: 300px;
    background: transparent;
    height: 298px;
  }
}
@media (min-width: 1200px) {
  .index-intro__download-inner {
    width: 300px;
    background: transparent;
    height: 345px;
    padding-top: 36px;
  }
}

.index-intro__download-main-link {
  display: block;
  height: 55px;
  font-weight: 700;
  font-size: 18px;
  line-height: 18px;
  text-align: center;
  color: #ffffff;
  border: 3px solid #ffffff;
  border-radius: 29px;
  padding: 15px 35px;
  width: 100%;
  max-width: 280px;
}
@media (min-width: 660px) {
  .index-intro__download-main-link {
    max-width: 360px;
  }
}
@media (min-width: 1200px) {
  .index-intro__download-main-link {
    font-size: 21px;
    padding: 15px 20px;
  }
}

.index-intro__download-icons {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-wrap: wrap;
  height: 65px;
  margin-top: 9px;
}

.index-intro__download-link:not(:first-child) {
  margin-left: 35px;
  padding-bottom: 2px;
}

.index-intro__download-text {
  font-weight: 300;
  font-size: 14px;
  line-height: 24px;
  text-align: center;
  max-width: 280px;
  margin: 0;
}

@media (min-width: 660px) {
  .index-intro__download-text {
    font-size: 18px;
    line-height: 24px;
    max-width: 300px;
  }
}
.ad__inner {
  background-color: #ffffff;
  padding: 10px 0 35px 0;
}

.advantages {
  position: relative;
  z-index: 1;
  padding: 78px 0 318px;
  overflow: hidden;
}
.advantages::before {
  position: absolute;
  bottom: 99%;
  left: -40px;
  z-index: -1;
  transform: rotate(175deg);
  display: block;
  height: 425px;
  width: 200%;
  content: "";
  background-color: #ffffff;
}
@media (min-width: 660px) {
  .advantages::before {
    bottom: 100%;
  }
}
@media (min-width: 1200px) {
  .advantages::before {
    bottom: 96%;
  }
}
.advantages::after {
  position: absolute;
  top: 0;
  z-index: -2;
  display: block;
  height: 100%;
  width: 100%;
  content: "";
  background: url("../img/advantages/screen.png") right 50% bottom -6px no-repeat, url("../img/advantages/phone.png") right 50% bottom -42px no-repeat;
  background-color: #f2f2f2;
}
@media (min-width: 660px) {
  .advantages {
    padding-top: 71px;
    padding-bottom: 42px;
  }
}

.advantages__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 660px) {
  .advantages__list {
    width: 310px;
    margin: 0 0 0 auto;
  }
}
@media (min-width: 1200px) {
  .advantages__list {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 0 30px;
  }
}

.advantages__item {
  text-align: center;
}
.advantages__item:not(:first-child) {
  margin-top: 31px;
}
@media (min-width: 660px) {
  .advantages__item:not(:first-child) {
    margin-top: 34px;
  }
}
@media (min-width: 1200px) {
  .advantages__item:not(:first-child) {
    margin-top: 0;
  }
}
@media (min-width: 660px) {
  .advantages__item {
    text-align: left;
  }
}
@media (min-width: 1200px) {
  .advantages__item {
    width: 240px;
    text-align: center;
    padding-top: 307px;
  }
}

.advantages__heading {
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
}
@media (min-width: 1200px) {
  .advantages__heading {
    font-size: 30px;
    line-height: 30px;
  }
}

.advantages__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  margin: 12px 0 0 0;
}
@media (min-width: 1200px) {
  .advantages__text {
    font-size: 18px;
    line-height: 30px;
    margin-top: 22px;
  }
}

.effect {
  display: none;
}
@media (min-width: 660px) {
  .effect {
    display: block;
  }
}
@media (min-width: 1200px) {
  .effect {
    background-color: #f2f2f2;
    padding-bottom: 40px;
  }
}

.effect__inner {
  padding-top: 95px;
  padding-bottom: 73px;
  background: url("../img/effect/timer.png") left 20px center no-repeat;
}
@media (min-width: 1200px) {
  .effect__inner {
    padding-top: 117px;
  }
}

.effect__info {
  width: 300px;
  margin: 0 10px 0 auto;
}
@media (min-width: 1200px) {
  .effect__info {
    width: 540px;
    margin-right: 30px;
  }
}

.effect__title {
  display: block;
  font-weight: 700;
  font-size: 30px;
  line-height: 36px;
  color: #000000;
}

@media (min-width: 660px) {
  .effect__subtitle {
    display: block;
  }
}
@media (min-width: 1200px) {
  .effect__subtitle {
    display: inline;
  }
}

.effect__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  margin-top: 12px;
  margin-bottom: 10px;
}
@media (min-width: 1200px) {
  .effect__text {
    font-size: 18px;
    line-height: 30px;
    margin-top: 22px;
  }
}

.effect__link {
  display: block;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  text-decoration-line: underline;
  color: #d22856;
}
@media (min-width: 1200px) {
  .effect__link {
    font-size: 18px;
    line-height: 30px;
    margin-top: 30px;
  }
}

.reviews {
  padding: 41px 0;
}
@media (min-width: 660px) {
  .reviews {
    padding-top: 0;
    padding-bottom: 17px;
  }
}
@media (min-width: 1200px) {
  .reviews {
    padding-top: 72px;
  }
  .reviews::before {
    display: block;
    width: 79px;
    height: 63px;
    content: "";
    margin: 0 auto;
    background-image: url("data:image/svg+xml,%0A%3Csvg width='79' height='63' viewBox='0 0 79 63' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M35.1468 6.97257L30.8388 0C28.3886 1.48142 0 12.0588 0 39.1293C0 46.2698 0.341426 63 18.9793 63C30.2162 63 32.9778 52.8473 32.9778 49.5783C32.9778 27.8508 11.5081 35.5838 11.5081 35.5838C11.5081 17.7771 31.3911 9.06631 35.1468 6.97257Z' fill='%23EFEFEF'/%3E%3Cpath d='M79 6.97257L74.7322 0C72.302 1.48142 43.8833 12.0588 43.8833 39.1293C43.8833 46.2698 44.2247 63 62.8726 63C74.0995 63 76.8611 52.8473 76.8611 49.5783C76.8611 27.8508 55.3914 35.5838 55.3914 35.5838C55.3914 17.7771 75.3146 9.06631 79 6.97257Z' fill='%23EFEFEF'/%3E%3C/svg%3E%0A");
  }
