.reviews__slider {
  position: relative;
}

.reviews__item {
  display: none;
  text-align: center;
}
.reviews__item--active {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 37px 185px;
  grid-template-areas: "author" "review";
  align-items: center;
}
@media (min-width: 660px) {
  .reviews__item--active {
    grid-template-rows: 170px 34px 37px;
    grid-template-areas: "review" "author" "author-about";
  }
}

.reviews__author {
  font-weight: 700;
  font-size: 18px;
  line-height: 30px;
  grid-area: author;
}
@media (min-width: 660px) {
  .reviews__author {
    font-size: 20px;
    line-height: 30px;
  }
}
@media (min-width: 1200px) {
  .reviews__author {
    font-size: 24px;
  }
}

.reviews__author-about {
  display: none;
  font-weight: 400;
  font-size: 14px;
  line-height: 30px;
  grid-area: author-about;
}
@media (min-width: 660px) {
  .reviews__author-about {
    display: block;
    align-self: flex-start;
    margin-top: 0;
  }
}
@media (min-width: 1200px) {
  .reviews__author-about {
    font-size: 18px;
    line-height: 30px;
  }
}

.reviews__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 30px;
  margin: 13px 0 0 0;
  grid-area: review;
}
@media (min-width: 660px) {
  .reviews__text {
    font-size: 18px;
    line-height: 30px;
  }
}
@media (min-width: 1200px) {
  .reviews__text {
    width: 690px;
    margin-left: auto;
    margin-right: auto;
  }
}

.reviews__button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: none;
  width: 22px;
  height: 41px;
  border: none;
  padding: 0;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 1200px) {
  .reviews__button {
    display: block;
    top: 40%;
  }
}
